<template>
  <div class="component-main">
    <div class="outer" ref="outerRef">
      <div class="inner">
        <div class="text half">
          <h1 class="font-nanum"><b class="color-point">퍼블레싱</b> ['pʌblesɪŋ]<br />: 명령어로 UI를 그리는 도구</h1>
          <h2 class="font-coding">
            <template v-if="store.state.account.login">
              <template v-if="store.state.site.lang === 'ko'">
                "create startup" 을 입력 후<br />
                "open startup" 을 입력해주세요
              </template>
              <template v-else>
                Enter "create startup" <br />
                then "open startup"
              </template>
            </template>
            <template v-else>
              <template v-if="store.state.site.lang === 'ko'">
                <div>아래의 콘솔 열기 버튼 클릭 후</div>
                "join" 혹은 "login" 을 입력해주세요
              </template>
              <template v-else>
                Click OPEN CONSOLE button below<br />
                then enter "join" or "login"
              </template>
            </template>
          </h2>
          <button class="btn btn-point d-none d-xl-inline-block" @click="callback('toggle-console')">
            <template v-if="store.state.site.lang === 'ko'">
              <span>콘솔 </span>
              <span v-if="visible">닫기</span>
              <span v-else>열기</span>
            </template>
            <template v-else>
              <span v-if="visible">CLOSE </span>
              <span v-else>OPEN </span>
              <span>CONSOLE</span>
            </template>
          </button>
          <router-link :to="`/${store.state.site.lang}/intro`" class="btn btn-point-line">{{ store.state.site.lang === "ko" ? "도구 소개" : "VIEW MORE" }}</router-link>
        </div>
        <div class="image half">
          <img src="/assets/img/image.main.svg" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { onMounted } from "@vue/runtime-core";

export default {
  props: {
    visible: Boolean,
    callback: Function,
    load: Function,
  },
  setup() {
    const store = useStore();
    const outerRef = ref(null);

    const setHeight = () => {
      outerRef.value.style.height = window.innerHeight - 60 + "px";
    };

    onMounted(setHeight);

    window.onresize = () => {
      setHeight();
    };

    return {
      store,
      outerRef,
    };
  },
};
</script>

<style lang="scss" scoped>
.component-main {
  position: relative;
  width: 100%;

  .outer {
    display: table;
    min-height: 550px;
    max-height: 1080px;
    width: 100%;

    .inner {
      display: table-cell;
      vertical-align: middle;

      .half {
        width: 50%;
        float: left;
      }

      h1 {
        font-size: 30px;
        position: relative;
        font-weight: normal;
        margin-bottom: 25px;
        line-height: 160%;

        b {
          font-weight: 900;
        }
      }

      h2 {
        font-size: 21px;
        position: relative;
        margin-bottom: 25px;
        line-height: 190%;
        color: #555;
      }

      .btn {
        margin: 10px 10px 10px 0;
        height: 50px;
        line-height: 50px;
        font-size: 15px;
        padding: 0;
        width: 170px;
        box-shadow: none;
        outline: 0;
        border-radius: 0;
        font-weight: normal;
      }

      &:after {
        clear: both;
        display: table;
        content: " ";
      }
    }
  }

  &[size="xxs"],
  &[size="xs"],
  &[size="sm"] {
    .outer .inner {
      > div {
        width: 100%;
        text-align: center;
      }

      .btn {
        margin-right: 0;
      }

      .image {
        margin-top: 50px;
        text-align: center;

        img {
          max-width: 500px;
        }
      }
    }
  }
}
</style>
